.card {
	position: relative;
  width: 270px;
  height: 403px;
  border: 1px solid #CDCDCD;
  box-sizing: border-box;
	border-radius: 3px;
	
	&:hover {
		box-shadow: 3px 3px 20px rgba(50, 50, 50, 0.25);
	}
}

.card:hover .card__popular-hover {
  display: block;
}

.card__photo {
	position: absolute;
  left: 18px;
	top: 50px;
	
	&--2 {
    top: 57px;
    left: 59px;
  }

  &--3 {
    top: 72px;
  }

  &--4 {
    top: 57px;
    left: 57px;
	}

	&--5 {
	top: 52px;
	left: 57px;
	}
}

.card__name {
	position: absolute;
  display: block;
  width: 100%;
  font-size: 20px;
  line-height: 24px;
  text-align: center;
  letter-spacing: 0.5px;
  bottom: 85px;
  color: #48494D;

  &--2 {
    width: 250px;
    bottom: 88px;
    left: 10px;
  }

  &--4 {
    bottom: 100px;
    left: 5px;
	}
	
	&--5 {
		width: 250px;
		left: 10px;
		bottom: 100px;
	}

	&--6 {
		width: 250px;
		left: 10px;
	}
}

.card__price {
	position: absolute;
  display: block;
  font-size: 30px;
  line-height: 36px;
  font-weight: bold;
  width: 100%;
  text-align: center;
  bottom: 30px;

  &--2 {
    margin-left: 75px;
    text-align: start;
  }
}

.card__icon-like {
	position: absolute;
  top: 17px;
  right: 17px;

  &--2 {
    top: 24px;
    right: 68px;
	}
	
	&--3 {
		top: 24px;
		right: 68px;
		fill: #FF7931;
		border: none;
	}
}

.card__icon-snow {
	position: absolute;
  top: 19px;
  right: 20px;
}

.card__banner-sale {
	position: absolute;
  left: -1px;
  top: -1px;
}

.card__buy {
	position: absolute;
  width: 60px;
  height: 40px;
  bottom: 0;
  right: 0;
  background: #FF7931;
  border-radius: 10px 0px 0px 0px;

  &--2 {
    bottom: -30px;
  }
}

.card__buy img {
  padding-top: 8px;
  padding-left: 20px;
}

.card__out-of-stock {
	position: absolute;
  display: block;
  width: 100%;
  bottom: 60px;
  font-weight: bold;
  font-size: 18px;
  line-height: 22px;
  letter-spacing: 0.7px;
  text-align: center;
  color: #48494D;
}

.card__link {
	position: absolute;
  display: block;
  text-align: center;
  text-decoration: none;
  font-family: "Proxima Nova", "Arial", sans-serif;
  font-size: 16px;
  line-height: 19px;
  color: #FF7931;
  bottom: 23px;
  left: 44px;
  border-bottom: 1px solid rgba(255, 121, 49, 0.2);
}

.card__popular-hover {
	position: absolute;
  display: none;
  width: 230px;
  height: 43px;
  padding-top: 17px;
  left: 20px;
  top: 122px;
  text-align: center;
  font-size: 20px;
  line-height: 24px;
  letter-spacing: 0.8px;
  color: #5F6063;
  background: #FFFFFF;
  opacity: 0.9;
  box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.2);
  border-radius: 3px;
}
