.additive h2 {
  font-size: 27px;
  line-height: 25px;
  font-weight: bold;
  letter-spacing: 0.5px; }

.additive__list {
  padding: 0;
  list-style: none;
  display: flex; }

.additive__button {
  background-color: transparent;
  border: none;
  font-size: 15px;
  line-height: 19px;
  letter-spacing: 0.5px;
  text-align: center;
  color: #48494D;
  margin: 0 25px 10px 0;
  border-bottom: 2px solid #fff;
  padding-bottom: 10px;
  cursor: pointer; }
  .additive__button:hover {
    text-shadow: 1px 0 0 #48494D;
    border-bottom: 2px solid #FF7931;
    border-right: none;
    border-left: none; }

.additive__products {
  position: relative;
  display: flex;
  justify-content: space-between;
  margin-bottom: 93px; }

.additive__products-button {
  position: absolute;
  font-size: 0;
  top: 166px;
  background: url("../../img/svg/icon-arrow.svg") 50% 50%/60% no-repeat;
  width: 35px;
  height: 35px;
  background-color: transparent;
  border: 1px solid #fff;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);
  border-radius: 50%; }
  .additive__products-button--left {
    transform: rotate(-180deg);
    left: -67px;
    top: 172px; }
  .additive__products-button--right {
    right: -67px; }
  .additive__products-button:hover {
    background-image: url("../../img/svg/icon-arrow-hover.svg") 50% 50%/60% no-repeat;
    background-color: #D7D9DF;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1); }
  .additive__products-button--right:hover {
    transform: rotate(0deg); }

.footer {
  background: #F4F4F6; }

.footer div ul {
  list-style: none; }

.footer__wrapper {
  display: flex;
  padding-top: 30px;
  padding-bottom: 20px; }

.footer__block1 {
  width: 292px;
  margin-right: 215px; }

.footer__block1 h2 {
  display: block;
  font-style: normal;
  width: 240px;
  font-weight: bold;
  font-size: 14px;
  line-height: 17px;
  letter-spacing: 0.5px;
  color: #48494D;
  margin-bottom: 34px; }

.footer__input {
  position: relative;
  margin-bottom: 27px; }

.footer__input-textarea {
  height: 33px;
  width: 162px;
  border: none;
  background-color: #FFFFFF;
  font-size: 14px;
  line-height: 17px;
  color: #A5A5A5;
  padding-left: 12px;
  padding-right: 10px; }

.footer__input-submit {
  position: absolute;
  width: 109px;
  height: 33px;
  border: none;
  background-color: #FF7931;
  font-weight: 600;
  font-size: 11px;
  line-height: 13px;
  letter-spacing: 0.12em;
  text-transform: uppercase;
  color: #FFFFFF;
  cursor: pointer; }

.footer__block1 span {
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 0.5px;
  color: #A5A5A5; }

.footer__block2 {
  display: flex;
  font-size: 13px; }

.footer__block2-info {
  margin-right: 25px;
  width: 130px; }
  .footer__block2-info--service {
    width: 180px; }
  .footer__block2-info--store {
    margin-right: 30px;
    width: 180px; }

.footer__block2-info h3 {
  margin-top: 12px;
  margin-bottom: 5px; }

.footer__block2-list {
  padding: 0;
  margin: 0; }
  .footer__block2-list--discounts {
    margin-top: 32px; }

.footer__block2-list li {
  margin-bottom: 5px; }

.footer__block2-list a {
  text-decoration: none;
  color: #48494D;
  font-size: 14px; }

.breadcrumbs {
  display: flex;
  list-style: none;
  margin: 0 0 40px 0;
  padding: 0; }

.breadcrumbs li:not(:last-of-type)::after {
  content: ">";
  margin: 0 10px;
  color: #CDCDCD; }

.breadcrumbs li a {
  text-decoration: none;
  font-family: "Proxima Nova", "Arial", sans-serif;
  font-size: 15px;
  line-height: 18px;
  color: #CDCDCD; }

.sort {
  position: relative;
  margin-bottom: 50px; }

.sort h2 {
  display: block;
  font-weight: bold;
  font-size: 30px;
  line-height: 36px;
  letter-spacing: 1.6px;
  color: #48494D;
  margin-bottom: 25px; }

.sort__wrapper {
  position: absolute;
  display: flex;
  right: 0;
  bottom: -5px; }

.sort__categories {
  display: flex; }

.sort__categories button {
  box-sizing: border-box;
  display: block;
  padding: 4px 20px;
  margin-right: 12px;
  background: #F4F4F6;
  border-radius: 50px;
  border: none;
  color: rgba(72, 73, 77, 0.7);
  font-size: 13px;
  line-height: 17px;
  letter-spacing: 1px;
  cursor: pointer; }

.sort__select-wrapper {
  position: relative;
  margin: 0 90px 0 auto;
  box-sizing: border-box; }

.sort__select {
  display: block;
  border: 1.5px solid #D7D9DF;
  padding: 8px 48px 7px 20px;
  appearance: none;
  background-color: transparent; }

.sort__select-wrapper::after {
  position: absolute;
  display: block;
  content: "";
  top: 12px;
  right: 17px;
  width: 12px;
  height: 8px;
  background-image: url("data:image/svg+xml,%3Csvg width='12' height='8' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M10.6 0L6 4.6 1.4 0 0 1.4l6 6 6-6L10.6 0z' fill='%23D7D9DF'/%3E%3C/svg%3E%0A");
  z-index: 0; }

.sort__radio {
  position: absolute; }
  .sort__radio--1 {
    right: 36px;
    bottom: 9px; }
  .sort__radio--2 {
    right: 2px;
    bottom: 9px; }

.sort__radio-input {
  appearance: none; }

.sort__radio-label {
  cursor: pointer; }

.sort__radio-label--1::before {
  content: "";
  display: block;
  width: 23px;
  height: 21px;
  background-image: url("data:image/svg+xml,%3Csvg width='23' height='21' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath stroke='%2348494D' stroke-width='2' d='M1 1h21v19H1z'/%3E%3Cpath fill='%2348494D' d='M6 5h4v4H6zM6 12h4v4H6zM13 5h4v4h-4zM13 12h4v4h-4z'/%3E%3C/svg%3E%0A");
  background-repeat: no-repeat;
  opacity: 0.3; }

.sort__radio-label--2::before {
  content: "";
  display: block;
  width: 23px;
  height: 19px;
  background-image: url("data:image/svg+xml,%3Csvg width='25' height='19' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='%2348494D'%3E%3Crect x='6' width='19' height='3' rx='1.5'/%3E%3Crect x='6' y='8' width='19' height='3' rx='1.5'/%3E%3Crect x='6' y='16' width='19' height='3' rx='1.5'/%3E%3Crect width='3' height='3' rx='1.5'/%3E%3Crect y='8' width='3' height='3' rx='1.5'/%3E%3Crect y='16' width='3' height='3' rx='1.5'/%3E%3C/g%3E%3C/svg%3E%0A");
  background-repeat: no-repeat;
  opacity: 0.3; }

.sort__radio-input--1:checked + .sort__radio-label--1::before {
  opacity: 1; }

.sort__radio-input--2:checked + .sort__radio-label--2::before {
  opacity: 1; }
